import { Component, OnInit } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatSnackBar } from "@angular/material/snack-bar";
import { Router } from "@angular/router";
import { Appointment } from "app/shared/models/appointment.model";
import { otpModel } from "app/shared/models/otp.model";
import { ApiService } from "app/shared/services/api.service";
import { LocalStoreService } from "app/shared/services/local-store.service";

@Component({
  selector: "app-appointment-id",
  templateUrl: "./appointment-id.component.html",
  styleUrls: ["./appointment-id.component.scss"],
})
export class AppointmentIdComponent implements OnInit {
  appointmentId: string;
  appointmentForm: FormGroup;
  transactionType: string;
  // appointmentresponse: any;
  oTpModel = new otpModel();
  otpResp: any;
  custStatus: any;
  errorStatus: number;
  trnType: string;
  constructor(
    private apiService: ApiService,
    private ls: LocalStoreService,
    private formBuilder: FormBuilder,
    private snack: MatSnackBar,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.appointmentForm = this.formBuilder.group({
      appointmentId: ["", Validators.required],
    });
    if (this.appointmentId) {
      this.fetchByApointment(this.appointmentId);
    }
  }
  fetchByApointment(appointmentId) {
    console.log(appointmentId);

    this.apiService
      .getAppointmentId(appointmentId.appointmentId)
      .subscribe((resp: any) => {
        console.log(resp);
        this.transactionType = resp;
        if (resp.data == null) {
          this.snack.open(resp.message, "OK", {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });
        }
       else if (resp.status == 500) {
          this.snack.open("Please Generate Token With In Respective Time Slot.", "OK", {
            duration: 4000,
            verticalPosition: "top",
            horizontalPosition: "right",
          });}
         else {
          this.ls.setItem("TransactionType", resp);
          this.appointmentId = resp.data.appointmentId;
          if (resp) {
            // this.goToOtp(resp);
            // this.router.navigate(["/sessions/otp"]);
            if (resp.data.trnType == "Cash Withdrawal") {
              this.router.navigateByUrl("/others/cw-cash-withdrawal");
            } else if (resp.data.trnType == "Cash Deposit") {
              this.router.navigateByUrl("/others/cd-cash-deposit");
            } else if (resp.data.trnType == "Cheque Withdrawal") {
              this.router.navigateByUrl("/others/cheq-withdrawal");
            } else if (resp.data.trnType == "Cheque Deposit") {
              this.router.navigateByUrl("/others/cheq-deposit");
            }
          }
        }
      },
      (err) => {console.log(err)});
    
  }
  goToOtp(phone) {
    if (phone.phoneNumber == "") {
      console.log("in if");
      this.snack.open("Enter Mobile Number to Login", "OK", {
        duration: 4000,
        verticalPosition: "top",
        horizontalPosition: "right",
      });
    } else {
      console.log("Phonenumber for OTP", phone, phone.phoneNumber);
      //this.customerPhoneNum = phone.phoneNumber;

      localStorage.setItem("phonenumFromLogin", phone.data.phoneNumber);
      this.oTpModel.source = "customer";
      this.oTpModel.source_key = "mobile";
      this.oTpModel.source_value = phone.data.phoneNumber;
      console.log("model", this.oTpModel);
      this.apiService.getOtp(this.oTpModel).subscribe(
        (otpResp) => {
          console.log("cust resp", otpResp);
          if (!otpResp?.icust || !otpResp.otpVal) {
            this.snack.open("No customer exists with this mobile number.", "", {
              duration: 4000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
          } else if (
            otpResp?.icust?.custStatus != "APPROVED" &&
            otpResp?.icust &&
            otpResp.otpVal
          ) {
            this.snack.open("Customer Id is not Approved.", "", {
              duration: 4000,
              verticalPosition: "top",
              horizontalPosition: "right",
            });
          } else {
            console.log("Response Success", otpResp);
            this.otpResp = otpResp;
            this.custStatus = this.otpResp.otpVal.userId;
            localStorage.setItem("customerValidation", this.custStatus);

            /* Added validation for un-registered mobile nummber is entered */
            // if (this.otpResp.otpVal && this.otpResp.otpVal.userId && this.otpResp.otpVal.userId.localeCompare("New Customer") == 0) {
            //   this.snack.open('User Phone Number is not Registered', 'OK', {
            //     duration: 4000,
            //     verticalPosition: 'top',
            //     horizontalPosition: 'right'
            //   });
            // }
            // else {
            // this.fpCaptureService.userDataService(otpResp);
            this.router.navigateByUrl("/sessions/otp");
            // }
          }
        },
        (error) => {
          console.log(error.status);
          this.errorStatus = error.status;
          if (this.errorStatus == 200) {
            // this.router.navigateByUrl('/sessions/otp');
          }
        }
      );
    }
  }
  goBack() {
    this.router.navigate(["/sessions/login/"]);
  }
}
